.wrapper {
    .wrapper();
}

.banners-grup {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: stretch;
    margin: 0 -10px;
}

.banners {
    flex: 1 1 380px;
    max-width: 580px;
    min-width: 480px;
    margin: 5px 5px;
    overflow: hidden;
    background: rgba(255, 255, 255, 1);
    color: rgba(0, 0, 0, 0.9);
    border: 1px solid rgba(0, 0, 0, 0.1);
    .banner {
        position: relative;
        .banner-img {
            width: 100%;
        }
        .banner-header {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 70px;
            text-align: center;
            font-size: 24px;
            opacity: 0;
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(255, 255, 255, 1));
            transition-duration: 1s;
        }
    }
    .banner-text {
        display: flex;
        height: ~"calc(100% - 170px)";
        flex-direction: column;
        align-items: center;
        padding: 5px;
        h4 {
            flex: 0 1 auto;
            font-size: 20px;
            margin: 5px 0;
            color: rgba(0, 0, 0, 1);
        }
        p {
            flex: 0 1 auto;
            font-size: 18px;
            font-weight: 100;
            margin: 2px 0;
            color: rgba(0, 0, 0, 1);
        }
    }
    .banner-align {
        margin-top: auto;
    }
    .banners-ref {
        text-decoration: none;
    }
    &:hover {
        border: 1px solid rgba(0, 0, 0, 0.2);
    }
    &:hover .banner-header {
        opacity: 1;
    }
}

.wrap-banners {
    margin-left: 5px;
    margin-right: 5px;
}

.grid-banners {
    .row-flex();
    justify-content: center; // align-items: stretch;  
    &__item {
        .col();
        .size(6);
        .size-lg(6);
        .size-md(10);
        .size-sm(12);
        .size-xs(12);
        margin-top: 10px;
    }
}

.banners_ {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
    height: 100%;
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: @color-white;
    &__pic {
        width: 100%;
        position: relative;
        &-img {
            width: 100%;
        }
        &-head {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 70px;
            text-align: center;
            font-size: 1.6rem;
            color: @color-isp;
            opacity: 0;
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(255, 255, 255, 1));
            transition-duration: 1s;
        }
    }
    &__text {
        display: flex;
        height: 100%;
        flex-direction: column;
        align-items: center;
        padding: 5px;
        h5,
        h6 {
            flex: 0 1 auto;
            margin: 5px 0;
        }
        &-align {
            margin-top: auto;
        }
    }
    &:hover {
        border: 1px solid rgba(0, 0, 0, 0.2);
    }
    &:hover &__pic-head {
        opacity: 1;
    }
}
