.info-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    ;
    align-content: stretch;
    font-weight: 200;
    .info {
        flex: 1 1 400px;
        margin: 5px 50px 5px 5px;
    }
    .access {
        flex: 0 1 200px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        border: 4px solid @color-isp; // padding: 1rem;
        min-height: 200px;
        text-align: center;
        &__head {
            width: auto;
            padding: 0.5rem 0 0 0;
        }
        &__value {
            width: 100%; // padding: 1rem;   
            // width: 200px;
            color: green; //    background-color: green;     
        }
        &__button {
            width: 100%;
            padding: 1rem 0; // padding: 1rem;
            background-color: @color-isp;
        }
    }
}

.grid-maps {
    .row-flex();
    justify-content: center;
    &__item {
        .col();
        .size(2);
        .size-lg(2);
        .size-md(2);
        .size-sm(4);
        .size-xs(5);
        .size-xxs(7);
        padding: 10px 5px;
        a {
            color: @color-isp;
        }
    }
}

.button {
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px; // font-size: 18px;
    &__picture {
        overflow: hidden;
        width: 100%;
        height: 100%;
        opacity: 0.5;
    }
    &__name {
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 0;
        left: 0;
        color: @color-isp;
    }
    &:hover &__picture {
        opacity: 1;
    }
}

.grid-news {
    .row-flex();
    justify-content: center;
    &__item {
        .col();
        .size(4);
        .size-lg(4);
        .size-md(4);
        .size-sm(12);
        padding: 10px 5px;
        &:nth-child(2){
            border-right: 1px solid rgba(0, 0, 0, 0.4);
            border-left: 1px solid rgba(0, 0, 0, 0.4);
            padding-left: 15px;
            .sm-block( {
                border-right: none;
                border-left: none;
            }
            )
        }
    }

    &__item--border:nth-child(odd){

    }
    &__item--border:nth-child(2){
        border-right: 1px solid rgba(0, 0, 0, 0.4);
        border-left: 1px solid rgba(0, 0, 0, 0.5);
        padding-left: 15px;
        .sm-block( {
            border-right: none;
            border-left: none;
        }
        )
    }
    &__date {
        font-size: 16px;
        font-weight: 700;
    }
}

.action-wrap {
    margin-top: -10px;
}

.grid-actions {
    .row-flex();
    justify-content: center; // align-items: stretch;
    &__item-first {
        .col();
        .size(7);
        .size-lg(7);
        .size-md(10);
        .size-sm(12); // order: 1;
    }
    &__item-second {
        .col();
        .size(5);
        .size-lg(5);
        .size-md(10);
        .size-sm(12);
    }
    .actions-col {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: space-between;
        height: 100%;
        .actions-button {
            flex: 1 1 auto;
            border: 1px solid @color-ispA;
            background: @color-ispA;
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: 80px;
            margin-top: 10px;
            .md-block( {
                display: none;
            }
            );
            p {
                height: 100%;
                margin: 0;
                font-size: 32px;
                color: rgba(0, 0, 0, 1);
                .material-icons {
                    font-size: 48px;
                    vertical-align: text-bottom;
                }
            }
            &:hover {
                background: @color-ispB;
                transition-duration: 0.5s;
            }
        }
    }
}

.actions-button--sm {
    flex: 1 1 auto;
    border: 1px solid rgba(255, 115, 0, 0.8);
    background: rgba(255, 115, 0, 0.8);
    display: none;
    align-items: center;
    justify-content: center;
    min-height: 80px;
    margin-top: 10px;
    .md-block( {
        display: flex;
    }
    );
    p {
        height: 100%;
        margin: 0;
        font-size: 32px;
        .sm(font-size, 24px);
        color: rgba(0, 0, 0, 1);
        .material-icons {
            font-size: 48px;
            .sm(font-size, 34px);
            vertical-align: text-bottom;
        }
    }
    &:hover {
        background: rgba(255, 115, 0, 1);
        transition-duration: 0.5s;
    }
}

