.card-elm(@el,
@n,
@col) {
    &@{el}:nth-child(@{n}) .price-card {
        border: 2px solid @col;
    }
    &@{el}:nth-child(@{n}) .price-card h5 {
        color: @col;
    }
    &@{el}:nth-child(@{n}) .price-card--background {
        background-color: @col;
        transition-duration: 0.5s;
    }
    &@{el}:nth-child(@{n}) .price-card--background h5 {
        color: @color-white;
    }
    &@{el}:hover .price-card {
        background-color: @color-white;
        transition-duration: 0.5s;
    }
    &@{el}:nth-child(@{n}):hover .price-card--background h5 {
        color: @col;
        transition-duration: 0.5s;
    }
}

