.price-card {
    height: 100%; // border: 2px solid @color-isp;
    border-radius: 1px;
    box-sizing: border-box;
    padding: 10px; // margin: 0 20px;
    font-size: 1.2rem;
    text-align: center;
    &--circle {
        border-radius: 100%;
        height: 0;
        margin: 0;
        padding: 50% 50%;
        max-width: 50%;
    }
    &__list {
        height: 100%;
        display: flex; // justify-content: center;
        align-items: center;
        flex-direction: column;
        &--circle {
            transform: translateY(-50%);
            height: auto;
        }
        p {
            flex: 0 1 auto;
            margin-bottom: 10px;
            font-size: 1em;
        }
        h5 {
            flex: 0 1 auto;
            margin-bottom: 10px;
            font-size: 1.2em; // color: @color-isp;
        }
        mark {
            font-size: 1.5em;
            background-color: inherit;
        }
        &--align {
            margin-top: auto; // flex: 1 1 auto;
            min-height: 1px;
        }
    }
    ;
    &__list--hove {}
    &:hover {
        background-color: @color-white;
    }
} // 
